@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #3f51b5 !important;
}
::-webkit-scrollbar {
  display: none;
}
.app_title {
  display: flex;
  justify-content: center;
  background-color: #673ab7;
  padding: 15px 20px;
  margin-bottom: 30px;
  box-shadow: inset 0 0 10px #000000;
}
.app_title h1 {
  font-family: "Monoton", cursive;
  letter-spacing: 4px;
  text-shadow: 4px 4px 5px yellow;
}
.app_footer {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #2196f3;
  padding: 10px 20px;
  box-shadow: inset 0 0 10px #000000;
}
.app_footer h4 {
  color: whitesmoke;
  font-family: monospace, sans-serif;
  font-size: 25px;
}
.app_footer span {
  color: red;
  font-size: 30px;
}

.not_found {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-family: fantasy, sans-serif;
}

@media (max-width: 600px) {
  .not_found {
    height: 60vh;
  }
}

.card {
  margin: 10px 10px;
  text-align: center;
  font-family: monospace, sans-serif;
  background: #9c27b0 !important;
  color: white;
  border-radius: 15px !important;
  transition: transform 0.5s ease-in; /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */ /* Firefox 3.5 - 3.6 */
  box-shadow: 3px 3px 3px 3px rgb(173, 168, 168);
}
.card > img {
  display: flex;
  justify-content: center;
  align-items: center;
}
.card-body {
  padding: 10px 20px;
  margin-top: 10px;
}
.card-title {
  font-size: 22px;
  letter-spacing: 3px;
  font-weight: 600;
  font-family: cursive;
  text-shadow: 2px 2px 4px #000000;
}
.card-text {
  font-size: 18px;
  letter-spacing: 1px;
}

.card:hover {
  transform: scale(1.05);
}

@media (max-width: 600px) {
  .card {
    width: 20rem;
    margin: 10px auto;
  }
}

.search_container {
  display: flex;
  justify-content: center;
  margin-bottom: 15px;
}
.search_container input {
  border-radius: 15px;
  border: none;
  width: 40%;
  padding: 6px 15px;
  background-color: rgb(163, 152, 224);
  color: black;
  font-size: 20px;
  box-shadow: inset 0 0 10px #000000;
}
input:focus {
  outline: none;
}

@media (max-width: 600px) {
  .search_container input {
    width: 70%;
    font-size: 18px;
  }
}

